.menu-opener {
  background-color: $colorBlue;
  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: 20px 0 48px;
  }

  &__top {
    margin-bottom: 48px;
  }

  &__list {
    margin: 0 0 48px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    @include openSans600;
    font-size: 22px;
    line-height: 22px;

    color: $colorWhite;
    text-decoration: none;

    letter-spacing: 2px;
    text-transform: uppercase;

    transition: border-color 0.3s;

    &:hover {
      border-color: $colorWhite;
    }
  }

  &__call {
    display: block;
    width: max-content;

    @include openSans600;
    font-size: 12px;
    line-height: 16px;

    letter-spacing: 2px;
    text-transform: uppercase;
    color: $colorWhite;
    text-decoration: none;

    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid $colorWhite;
    }
  }

}
